.buttons {
    border: 1px solid black;
    border-radius: 15px;
    padding: 10px;
    margin: 10px;
}

.buttons:hover {
    cursor: pointer;
}

#seasonstats, #weeklyleaderboard, #weeklypicks {
    display: none;
}

.collapsible {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
  }
  
  .active, .collapsible:hover {
    background-color: #ccc;
  }
  
  .weeklyPick-content {
    display: none;
    padding: 0 18px;
    overflow: hidden;
    background-color: #f1f1f1;
  }

  .collapsible:after {
    content: '\02795'; 
    font-size: 13px;
    color: white;
    float: right;
    margin-left: 5px;
  }
  
  .active:after {
    content: "\2796"; 
  }